import { Component, OnInit, HostListener } from '@angular/core';
import { AuthenticationService } from '../../authentication.service';
import { Router } from '@angular/router';

enum Tab {
  Login = 0,
  Register
};

@Component({
  selector: 'app-mobile-login-popup',
  templateUrl: './mobile-login-popup.component.html',
  styleUrls: ['./mobile-login-popup.component.scss']
})
export class MobileLoginPopupComponent implements OnInit {
  tab: Tab;
  username: string;
  password: string;
  password2: string;
  loading: boolean = false;
  errorMessage: string;
  hidePhoto: boolean = false;


  public Tab = Tab;

  constructor(private auth: AuthenticationService, private router: Router) { }

  ngOnInit() {
    this.tab = Tab.Login;
  }

  changeTab(val: Tab): void {
    this.tab = val;
  }

  submit(): void {
    this.errorMessage = null;
    this.loading = true;
    if(this.tab == Tab.Register) this.register();
    else this.login();
  }

  login(): void {
    this.auth.login(this.username, this.password).then(this.successHandler).catch(this.errorHandler);
  }

  register(): void {
    this.auth.register(this.username, this.password).then(response => {
      this.router.navigate([{ outlets: { popup: null }}]);
    });
  }

  @HostListener('window:resize', ['$event'])
  onResize(ev) {
    if(ev.target.innerHeight/ev.target.innerWidth < 1.6) {
      this.hidePhoto = true;
    }
    else {
      this.hidePhoto = false;
    }
  }

  successHandler = (response) => {
    this.loading = false;
    this.router.navigate([{ outlets: { popup: null }}]);
  }

  errorHandler = (error) => {
    this.loading = false;
    this.errorMessage = error;
  }
}



