import { Component, OnInit } from '@angular/core';
import { Request } from '../../request';
import { RequestService } from '../../request.service';

@Component({
  selector: 'app-panel-request-list',
  templateUrl: './panel-request-list.component.html',
  styleUrls: ['./panel-request-list.component.css']
})
export class PanelRequestListComponent implements OnInit {
  loading: boolean;

  constructor(private requestService: RequestService) { }

  getRequests(): void {
    if(!this.requestService.requests){
      this.loading = true;
      this.requestService.filterRequests().then((requests) => {
        this.loading = false;
      });
    }
  }

  ngOnInit() {
    this.getRequests();
  }

}



