import { NgModule } from '@angular/core';
import { RouterModule, Routes }   from '@angular/router';

import { PanelAppComponent } from './panel/panel-app.component';
import { PanelRequestListComponent } from './panel/request-list/panel-request-list.component';
import { PanelRequestDetailsComponent } from './panel/request-details/panel-request-details.component';
import { PanelActionDetailsComponent } from './panel/action-details/panel-action-details.component';
import { PanelActionListComponent } from './panel/action-list/panel-action-list.component';
import { PanelEmployeeListComponent } from './panel/employee-list/panel-employee-list.component';
import { PanelEmployeeDetailsComponent } from './panel/employee-details/panel-employee-details.component';
import { PanelUserListComponent } from './panel/panel-user-list/panel-user-list.component';
import { PanelUserDetailsComponent } from './panel/panel-user-details/panel-user-details.component';
import { PanelPopupComponent } from './panel/panel-popup/panel-popup.component';
import { PanelAssignEmployeePopupComponent } from './panel/panel-assign-employee-popup/panel-assign-employee-popup.component';
import { PanelAddEmployeePopupComponent } from './panel/panel-add-employee-popup/panel-add-employee-popup.component';
import { PanelLoginComponent } from './panel/panel-login/panel-login.component';

import { MobileAppComponent } from './mobile/mobile-app.component';
import { MobileHomeComponent } from './mobile/mobile-home/mobile-home.component';
import { MobileRequestListComponent } from './mobile/mobile-request-list/mobile-request-list.component';
import { MobileAddRequestFormComponent } from './mobile/mobile-add-request-form/mobile-add-request-form.component';
import { MobilePopupComponent } from './mobile/mobile-popup/mobile-popup.component';
import { MobileLoginPopupComponent } from './mobile/mobile-login-popup/mobile-login-popup.component';
import { MobilePayPopupComponent } from './mobile/mobile-pay-popup/mobile-pay-popup.component';
import { MobileRatingPopupComponent } from './mobile/mobile-rating-popup/mobile-rating-popup.component';

const appRoutes: Routes = [
  {
    path: 'panel',
    component: PanelAppComponent,
    children: [
      { path: '', component: PanelRequestListComponent },
      { path: 'employees', component: PanelEmployeeListComponent },
      { path: 'actions', component: PanelActionListComponent },
      { path: 'users', component: PanelUserListComponent },
      { path: 'login', component: PanelLoginComponent },
    ]
  },
  {
    path: '',
    component: MobileAppComponent,
    children: [
      { path: '', component: MobileHomeComponent },
      { path: 'history', component: MobileRequestListComponent },
      { path: 'new/:type', component: MobileAddRequestFormComponent },
    ]
  },
  {
    path: 'modal',
    component: MobilePopupComponent,
    outlet: 'popup',
    children: [
      { path: 'login', component: MobileLoginPopupComponent },
      { path: 'rating', component: MobileRatingPopupComponent },
      { path: 'pay', component: MobilePayPopupComponent },
    ]
  },
  {
    path: 'panel/modal',
    component: PanelPopupComponent,
    outlet: 'popup',
    children: [
      { path: 'assign-employee', component: PanelAssignEmployeePopupComponent },
      { path: 'add-employee', component: PanelAddEmployeePopupComponent },
    ]
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes, { useHash: true })
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }



