import { Component, OnInit } from '@angular/core';
import { ActionService } from '../action.service';
import { AuthenticationService } from '../authentication.service';
import { User, UserRole } from '../user';

@Component({
  selector: 'app-mobile',
  templateUrl: './mobile-app.component.html',
  styleUrls: ['./mobile-app.component.css']
})
export class MobileAppComponent implements OnInit{
  loading: number = 2;

  constructor(private actionService: ActionService, private auth: AuthenticationService) { }

  ngOnInit() {

    this.actionService.loadActions().then(res => this.loading--);

    this.auth.loginWithSession().then((user) => {
      if(user.role != UserRole.Customer){
        this.auth.logout();
      }
      this.loading--;
    }).catch((error) => {
      this.loading--;
      this.auth.logout();
    });
  }

}



