import { Pipe, PipeTransform } from '@angular/core';
import { RequestType } from './request';
import { Action } from './action';

@Pipe({
  name: 'actionTypeFilter',
  pure: false,
})
export class ActionTypeFilterPipe implements PipeTransform {

  transform(allActions: Action[], requestType: RequestType): Action[] {
    if(allActions == null) return;
    return allActions.filter(action => action.requestType == requestType);
  }

}



