import { Component, OnInit, Input } from '@angular/core';
import { Request, RequestStep } from '../../request';

@Component({
  selector: 'app-stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.scss']
})
export class StepperComponent implements OnInit {
  step: RequestStep;
  @Input() request: Request;
  @Input() type: string;
  public RequestStep = RequestStep;

  constructor() { }

  ngOnInit() {
    if(this.request.comment) this.step = RequestStep.Finished;
    else if(this.request.paymentType && this.request.paymentType != 0) this.step = RequestStep.WaiteForRating;
    else if(this.request.employee) this.step = RequestStep.WaiteForPay;
    else this.step = RequestStep.WaiteForEmployee;
  }

}



