import { Component, OnInit } from '@angular/core';
import { Action } from '../../action';
import { ActionService } from '../../action.service';
import { RequestType } from '../../request';

@Component({
  selector: 'app-panel-action-list',
  templateUrl: './panel-action-list.component.html',
  styleUrls: ['./panel-action-list.component.css']
})
export class PanelActionListComponent implements OnInit {
  requestType = RequestType;

  constructor(private actionService: ActionService) { }


  ngOnInit() {
  }

  addNewAction(type: RequestType): void {
    this.actionService.addNewAction(type);
  }

}



