import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from '@angular/router';
import { User, UserRole } from '../user';
import { ActionService } from '../action.service';
import { EmployeeService } from '../employee.service';
import { ServerConnectionService } from '../server-connection.service';
import { AuthenticationService } from '../authentication.service';

@Component({
  selector: 'app-root',
  templateUrl: './panel-app.component.html',
  styleUrls: ['./panel-app.component.css']
})
export class PanelAppComponent implements OnInit{
  loading: number;

  constructor(
    private actionService: ActionService,
    private employeeService: EmployeeService,
    private serverConnectionService: ServerConnectionService,
    private auth: AuthenticationService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.loading = 3;

    this.auth.loginWithSession().then((user) => {
      if(user.role != UserRole.Admin){
        this.auth.logout();
        this.router.navigate(["/panel/login"]);
      }
      this.loading--;
    }).catch((error) => {
      this.auth.logout();
      this.router.navigate(["/panel/login"]);
    });

    this.actionService.loadActions().then(res => this.loading--);

    this.employeeService.loadEmployees().then(res => this.loading--);
  }

}



