import { Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Employee } from '../../employee';
import { EmployeeService } from '../../employee.service';

@Component({
  selector: 'app-panel-employee-list',
  templateUrl: './panel-employee-list.component.html',
  styleUrls: ['./panel-employee-list.component.css']
})
export class PanelEmployeeListComponent implements OnInit {
  
  constructor(private employeeService: EmployeeService, private router: Router) {

  }

  ngOnInit() {

  }

  openNewPopup(): void {
    let navigationExtras: NavigationExtras = {
      skipLocationChange: true
    };
    this.router.navigate([{ outlets: { popup: 'panel/modal/add-employee' }}], navigationExtras);
  }
}



