import { Component, OnInit } from '@angular/core';
import { RequestType } from '../../request';
import { BrowserThemeService } from '../../browser-theme.service';
import { AuthenticationService } from '../../authentication.service';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
  selector: 'app-mobile-home',
  templateUrl: './mobile-home.component.html',
  styleUrls: ['./mobile-home.component.scss'],
  animations: [
    trigger('navigationDrawerState', [
      state('close', style({
        transform: 'translateX(100%)'
      })),
      state('open',   style({
        transform: 'translateX(0%)'
      })),
      transition('open => close', animate('100ms ease-in')),
      transition('close => open', animate('100ms ease-out'))
    ]),
    trigger('navigationWrapperState', [
      state('close', style({
        display: 'none',
      })),
      state('open',   style({
        display: 'block'
      })),
    ])
  ]
})
export class MobileHomeComponent implements OnInit {
  navigationDrawerState = 'close';

  public RequestType = RequestType;

  constructor(private themeService: BrowserThemeService, private auth: AuthenticationService) { }

  ngOnInit() {
    this.themeService.changeColor("#000000");
  }

  toggleNavigationDrawer(): void {
    this.navigationDrawerState = this.navigationDrawerState == 'close' ? 'open' : 'close';
  }

  logout(): void {
    this.auth.logout();
    this.toggleNavigationDrawer();
  }

}



