import { Pipe, PipeTransform } from '@angular/core';
import { Employee } from './employee';

@Pipe({
  name: 'employeeSearch'
})
export class EmployeeSearchPipe implements PipeTransform {

  transform(employees: Employee[], query: string): Employee[] {
    if (!employees) return [];
    return employees.filter(em => em.name.search(query) > -1 || (em.description && em.description.search(query) > -1) || (em.address && em.address.search(query) > -1));
  }

}



