import { Action } from './action';
import { Employee } from './employee';
import { User } from './user';

export enum RequestStatus {
    Waiting = 0,
    Set,
    Accepted,
    Done
}

export enum RequestStep {
  WaiteForEmployee = 0,
  WaiteForPay,
  WaiteForRating,
  Finished
}

export enum RequestType {
  Water = 0,
  Electricity,
  Construction
}

export enum PaymentType {
  Online = 0, //online
  Cash        // naghdi
}

export class Request {

  constructor() {

  }

  id: number;
  createDate: object;
  name: string;
  phone: string;
  address: string;
  status: RequestStatus;
  type: RequestType;
  code: string;                 // code moarref
  isImidiate: boolean;          // fori
  description: string;          // tozihati ke tavasote karbar dar app por mishe
  action: Action;             // har daste (tasisat, bannayi, bargh) shamele zirdaste hayi hast ke be soorate dynamic por mishe va esmesh actione
  paymentType: PaymentType;
  paymentPrice: number;         // mablaghe kar ke tavasote panel moshakhas mishe
  responseDescription: string;  // tozihati ke tavasote panel bayad por beshe va hamrah ba mablagh va nirooye moshakhas shode ersal mishe
  employee: Employee;           // nirooyi ke movazaf be anjam in kar shode va az taraf panel set mishe
  rating: number;               // rating ke tavasote karbar dar enteha takmil mishe
  comment: string;              // comment nahayi ke bad az etmam kar tavasote karbar hamrah ba rating ersal mishe
  customer: User;
  file: any;

  getStep(): RequestStep {
    if(this.comment) return RequestStep.Finished;
    if(this.paymentType && this.paymentType != 0) return RequestStep.WaiteForRating;
    if(this.employee) return RequestStep.WaiteForPay;
    return RequestStep.WaiteForEmployee;
  }
}



