import { Injectable } from '@angular/core';
import { Action } from './action';
import { RequestType } from './request';
import { ServerResponse } from './server-response';
import { ServerConnectionService } from './server-connection.service';

@Injectable()
export class ActionService {
  actions: Action[];

  constructor(private serverConnection: ServerConnectionService) { }

  loadActions(): Promise<Action[]> {
    return this.serverConnection.get('actions').then((response) => this.actions = response as Action[]);
  }

  getActionById(id: number) {
    return this.actions.find(x => x.id == id);
  }

  updateAction(action: Action): Promise<any> {
    return this.serverConnection.post('actions/edit', action);
  }

  removeAction(action: Action): Promise<any> {
    this.removeNewAction(action);
    return this.serverConnection.post('actions/remove', action);
  }

  addNewAction(type: RequestType): void {
    this.actions.push(new Action(type));
  }

  removeNewAction(action: Action): void {
    this.actions.splice(this.actions.indexOf(action), 1);
  }

  saveNewAction(action: Action): Promise<any> {
    return this.serverConnection.post('actions/add', action).then((res) => {
      action.id = res;
    });
  }

}



