import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule }   from '@angular/router';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

// Imports for loading & configuring the in-memory web api
// import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
// import { InMemoryDataService }  from './in-memory-data.service';

import { PersianDateModule } from 'angular2-persian-date';
import { StarRatingModule } from 'angular-star-rating';
import { SimpleNotificationsModule } from 'angular2-notifications';
import { JasperoConfirmationsModule  } from '@jaspero/ng2-confirmations';
import { Ng2Webstorage } from 'ng2-webstorage';
import { ImageCropperComponent } from 'ng2-img-cropper';
import { Ng2DeviceDetectorModule, Ng2DeviceService } from 'ng2-device-detector';


import { AppRoutingModule }        from './app-routing.module';

import { AppComponent } from './app.component';
import { ServerConnectionService } from './server-connection.service';
import { AuthenticationService } from './authentication.service';
import { BrowserThemeService } from './browser-theme.service';
import { RequestService } from './request.service';
import { RequestStatusPipe } from './request-status.pipe';
import { ActionService } from './action.service';
import { ActionTypeFilterPipe } from './action-type-filter.pipe';
import { EmployeeService } from './employee.service';
import { UserService } from './user.service';

import { PanelAppComponent } from './panel/panel-app.component';
import { PanelRequestListComponent } from './panel/request-list/panel-request-list.component';
import { PanelRequestDetailsComponent } from './panel/request-details/panel-request-details.component';
import { PanelActionDetailsComponent } from './panel/action-details/panel-action-details.component';
import { PanelActionListComponent } from './panel/action-list/panel-action-list.component';
import { PanelEmployeeListComponent } from './panel/employee-list/panel-employee-list.component';
import { PanelEmployeeDetailsComponent } from './panel/employee-details/panel-employee-details.component';
import { MobileAppComponent } from './mobile/mobile-app.component';
import { MobileHomeComponent } from './mobile/mobile-home/mobile-home.component';
import { MobileRequestListComponent } from './mobile/mobile-request-list/mobile-request-list.component';
import { MobileRequestDetailsComponent } from './mobile/mobile-request-details/mobile-request-details.component';
import { MobileAddRequestFormComponent } from './mobile/mobile-add-request-form/mobile-add-request-form.component';
import { StepperComponent } from './mobile/stepper/stepper.component';
import { MobileLoginPopupComponent } from './mobile/mobile-login-popup/mobile-login-popup.component';
import { MobilePopupComponent } from './mobile/mobile-popup/mobile-popup.component';
import { MobileRatingPopupComponent } from './mobile/mobile-rating-popup/mobile-rating-popup.component';
import { MobilePayPopupComponent } from './mobile/mobile-pay-popup/mobile-pay-popup.component';
import { PanelPopupComponent } from './panel/panel-popup/panel-popup.component';
import { PanelAssignEmployeePopupComponent } from './panel/panel-assign-employee-popup/panel-assign-employee-popup.component';
import { PanelAddEmployeePopupComponent } from './panel/panel-add-employee-popup/panel-add-employee-popup.component';
import { PanelLoginComponent } from './panel/panel-login/panel-login.component';
import { EmployeeSearchPipe } from './employee-search.pipe';
import { PanelUserListComponent } from './panel/panel-user-list/panel-user-list.component';
import { PanelUserDetailsComponent } from './panel/panel-user-details/panel-user-details.component';
import { UserSearchPipe } from './user-search.pipe';

@NgModule({
  declarations: [
    AppComponent,
    PanelAppComponent,
    PanelRequestListComponent,
    PanelRequestDetailsComponent,
    RequestStatusPipe,
    PanelActionDetailsComponent,
    PanelActionListComponent,
    ActionTypeFilterPipe,
    PanelEmployeeListComponent,
    PanelEmployeeDetailsComponent,
    MobileAppComponent,
    MobileHomeComponent,
    MobileRequestListComponent,
    MobileRequestDetailsComponent,
    MobileAddRequestFormComponent,
    StepperComponent,
    MobileLoginPopupComponent,
    MobilePopupComponent,
    MobileRatingPopupComponent,
    MobilePayPopupComponent,
    PanelPopupComponent,
    PanelAssignEmployeePopupComponent,
    PanelAddEmployeePopupComponent,
    PanelLoginComponent,
    ImageCropperComponent,
    EmployeeSearchPipe,
    PanelUserListComponent,
    PanelUserDetailsComponent,
    UserSearchPipe,
  ],
  imports: [
    BrowserModule,
    PersianDateModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    Ng2Webstorage,
    SimpleNotificationsModule.forRoot(),
    StarRatingModule.forRoot(),
    JasperoConfirmationsModule,
    Ng2DeviceDetectorModule.forRoot(),
    // InMemoryWebApiModule.forRoot(InMemoryDataService),
    AppRoutingModule
  ],
  providers: [
    RequestService,
    ActionService,
    EmployeeService,
    UserService,
    ServerConnectionService,
    BrowserThemeService,
    AuthenticationService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



