import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { RequestOptions } from '@angular/http';
import { ServerResponse } from './server-response';

import 'rxjs/add/operator/toPromise';

const BASE_URL: string = '/api/ham/index.php/';
// const IP: string = 'http://88.99.244.66';
const IP: string = 'http://hamservice.tk';
const PORT: string = ':8080';

// const IP: string = 'http://192.168.10.99';
// const PORT: string = ':80';

@Injectable()
export class ServerConnectionService {
  options: RequestOptions;

  constructor(private http: Http) {
    this.options = new RequestOptions();
    this.options.withCredentials = true;
  }

  get(url: string, options: {} = {}): Promise<any> {
    return this.http.get(IP + PORT + BASE_URL + url, this.options)
      .toPromise()
      .then(this.handleSuccess)
      .catch(this.handleError);
  }

  put(url: string, data: Object, options: {} = {}): Promise<any> {
    return this.http.put(IP + PORT + BASE_URL + url, data, this.options)
      .toPromise()
      .then(this.handleSuccess)
      .catch(this.handleError);
  }

  post(url: string, data: Object, options: {} = {}): Promise<any> {
    return this.http.post(IP + PORT + BASE_URL + url, data, this.options)
      .toPromise()
      .then(this.handleSuccess)
      .catch(this.handleError);
  }

  delete(url: string, options: {} = {}): Promise<any> {
    return this.http.delete(IP + PORT + BASE_URL + url, this.options)
      .toPromise()
      .then(this.handleSuccess)
      .catch(this.handleError);
  }

  private handleSuccess(response: any): Promise<any> {
    response = response.json() as ServerResponse;
    if(response.error){
      return Promise.reject(response);
    }
    return Promise.resolve(response.data);
  }
  private handleError(error: any): Promise<any> {
    return Promise.reject(error.message || error);
  }
}



