import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RequestType as EmployeeType } from '../../request';
import { Employee } from '../../employee';
import { EmployeeService } from '../../employee.service';

@Component({
  selector: 'app-panel-add-employee-popup',
  templateUrl: './panel-add-employee-popup.component.html',
  styleUrls: ['./panel-add-employee-popup.component.scss'],
})
export class PanelAddEmployeePopupComponent implements OnInit {
  employee: Employee;
  EmployeeType = EmployeeType;

  constructor(private route: ActivatedRoute, private employeeService: EmployeeService, private router: Router) {
  }

  ngOnInit() {

    this.route.queryParams.subscribe((params) => {
      if(params.employeeId){
        this.employee = this.employeeService.getEmployeeById(params.employeeId);
      }
      else{
        this.employee = new Employee();
      }
    });
  }

  add(): void {
    if(this.employee.id == null){
      this.employeeService.addEmployee(this.employee);
    }
    else{
      this.employeeService.updateEmployee(this.employee);
    }
    this.router.navigate([{ outlets: { popup: null }}]);
  }

}



