import { Component, OnInit, Input } from '@angular/core';
import { RequestType } from '../../request';
import { Employee } from '../../employee';
import { EmployeeService } from '../../employee.service';
import { NavigationExtras, Router } from '@angular/router';
import { ConfirmationService } from '@jaspero/ng2-confirmations';
import { ImageCropperComponent, CropperSettings } from 'ng2-img-cropper';

@Component({
  selector: 'app-panel-employee-details',
  templateUrl: './panel-employee-details.component.html',
  styleUrls: ['./panel-employee-details.component.css']
})
export class PanelEmployeeDetailsComponent implements OnInit {
  @Input() employee: Employee;
  cropperSettings: CropperSettings;
  cropping: boolean = false;
  croppedImage: any;
  EmployeeType = RequestType;

  constructor(
    private router: Router,
    private confirmation: ConfirmationService,
    private employeeService: EmployeeService
  ) { }

  ngOnInit() {
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.width = 96;
    this.cropperSettings.height = 96;
    this.cropperSettings.croppedWidth = 96;
    this.cropperSettings.croppedHeight = 96;
    this.cropperSettings.canvasWidth = 256;
    this.cropperSettings.canvasHeight = 220;
    this.croppedImage = {};
  }

  openEdit(): void {
    let navigationExtras: NavigationExtras = {
      skipLocationChange: true,
      queryParams: { 'employeeId': this.employee.id },
    };
    this.router.navigate([{ outlets: { popup: 'panel/modal/add-employee' }}], navigationExtras);
  }

  openDeleteConfirmation(): void {
    this.confirmation.create(
      ' ' + this.employee.name + '',
      '       '
    ).subscribe((ans: any) => {
      if(ans.resolved == true) {
        this.employeeService.removeEmployee(this.employee).catch(err => {
          alert("no");
        });
      }
    });
  }

  saveCropping(): void {
    var imageBeforeSend = this.croppedImage.image;
    this.employee.imageUrl = this.croppedImage.image.substr(this.croppedImage.image.indexOf(',') + 1);
    this.employeeService.updateImageUrl(this.employee).then(() => {
      this.employee.imageUrl = imageBeforeSend;
    });
    this.cropping = false;
  }
}



