import { Component, OnInit, HostListener, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { User } from '../../user';
import { Request, RequestType } from '../../request';
import { RequestService } from '../../request.service';
import { AuthenticationService } from '../../authentication.service';
import { Router } from '@angular/router';
import { BrowserThemeService } from '../../browser-theme.service';
import { Ng2DeviceService } from 'ng2-device-detector';

@Component({
  selector: 'app-mobile-request-list',
  templateUrl: './mobile-request-list.component.html',
  styleUrls: ['./mobile-request-list.component.scss']
})
export class MobileRequestListComponent implements OnInit {
  selectedRequestIndex: number;
  user: User;
  toolbarBackground: string = 'transparent';
  os: string;

  constructor(
    private themeService: BrowserThemeService,
    private requestService: RequestService,
    private router: Router,
    private authenticationService: AuthenticationService,
    private deviceService: Ng2DeviceService,
    @Inject(DOCUMENT) private document: Document
  ) { }

  ngOnInit() {
    this.user = this.authenticationService.user;

    this.os = this.deviceService.getDeviceInfo().os;

    this.requestService.getRequests().then((requests) => {
      if(requests.length > 0){
        this.toolbarBackground = this.getRequestColor(requests[0]);
      }
    }).catch((error) => {
      this.router.navigateByUrl("/");
    });
  }

  private setBrowserColor(request: Request): void {
    this.themeService.changeColor(this.getRequestColor(request));
  }

  private getRequestColor(request: Request): string {
    var type = Number(request.type);
    switch(type) {
      case RequestType.Water:
        return this.themeService.tealColor;
      case RequestType.Construction:
        return this.themeService.redColor;
      case RequestType.Electricity:
        return this.themeService.orangeColor;
    }
  }

  @HostListener("window:scroll", [])
  onWindowScroll() {
    let scrollPos = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    var items = document.getElementsByClassName('history-item');
    if(items.length == 0 ) return;
    var currentItem = 0;
    for (let i = 0; i < items.length; i++) {
      var item: any = items[i];
      if(item.offsetTop - scrollPos < 96) currentItem = i;
    }
    if(currentItem != this.selectedRequestIndex){
      this.setBrowserColor(this.requestService.requests[currentItem]);
      this.selectedRequestIndex = currentItem;
    }
  }

}



