import { Component, OnInit, Input } from '@angular/core';
import { Action } from '../../action';
import { ActionService } from '../../action.service';
import { ConfirmationService } from '@jaspero/ng2-confirmations';

@Component({
  selector: 'app-panel-action-details',
  templateUrl: './panel-action-details.component.html',
  styleUrls: ['./panel-action-details.component.css']
})
export class PanelActionDetailsComponent implements OnInit {
  editing: boolean;
  tempTitle: string;
  @Input() action: Action;

  constructor(private confirmation: ConfirmationService, private actionService: ActionService) {
  }

  ngOnInit() {
    this.editing = this.action.id == null ? true : false;
  }

  edit(): void{
    this.tempTitle = this.action.title;
    this.editing = true;
  }

  remove(): void {
    this.confirmation.create(
      ' ' + this.action.title + '',
      '       '
    ).subscribe((ans: any) => {
      if(ans.resolved == true) {
        this.actionService.removeAction(this.action).catch(err => {
          alert("no");
        });
      }
    });
  }

  save(): void{
    if(this.action.title == null || this.action.title == '') return;
    this.editing = false;
    if(this.action.id) {
      this.actionService.updateAction(this.action).catch(error => {
        this.action.title = this.tempTitle;
      });
    }
    else {
      this.actionService.saveNewAction(this.action).catch(error => {
        this.action.title = this.tempTitle;
      });
    }
  }

  cancel(): void{
    if(this.action.id == null) {
      this.actionService.removeNewAction(this.action);
    }
    else {
      this.action.title = this.tempTitle;
      this.editing = false;
    }
  }

}



