import { Component, OnInit, HostListener } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Request } from '../../request';
import { RequestService } from '../../request.service';
import { NotificationsService } from 'angular2-notifications';

@Component({
  selector: 'app-mobile-rating-popup',
  templateUrl: './mobile-rating-popup.component.html',
  styleUrls: ['./mobile-rating-popup.component.scss']
})
export class MobileRatingPopupComponent implements OnInit {
  comment: string;
  rating: number;
  request: Request;
  hidePhoto: boolean = false;
  
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private requestService: RequestService,
    private notificationService: NotificationsService
  ) { }

  ngOnInit() {
    this.rating = 3;
    this.route.queryParams.subscribe((params) => {
      this.request = this.requestService.getRequestById(params.requestId);
    });
  }

  rate() {
    this.requestService.rate(this.request, this.comment, this.rating).then(() => {
      this.notificationService.success(
        " ",
        "     "
      );
    }).catch((err) => {
      this.notificationService.error(
        " ",
        err
      );
    });
    this.router.navigate([{ outlets: { popup: null }}]);
  }

  @HostListener('window:resize', ['$event'])
  onResize(ev) {
    if(ev.target.innerHeight/ev.target.innerWidth < 1.6) {
      this.hidePhoto = true;
    }
    else {
      this.hidePhoto = false;
    }
  }

  rateChange(ev: any) {
    this.rating = ev.rating;
  }
}



