import { Injectable } from '@angular/core';
import { Request, PaymentType } from './request';
import { ServerResponse } from './server-response';
import { ServerConnectionService } from './server-connection.service';
import { Employee } from './employee';

@Injectable()
export class RequestService {
  requests: Request[];
  constructor(private serverConnection: ServerConnectionService) { }

  getRequests(): Promise<Request[]> {
    return this.serverConnection.get('requests', {withCredentials: true}).then((response) => this.requests = response as Request[]);
  }

  getRequestById(id: number) {
    return this.requests.find(x => x.id == id);
  }

  filterRequests(from: number = null, to: number = null, filter: any = null): Promise<Request[]> {
    return this.serverConnection.post('requests', {withCredentials: true}).then((response) => this.requests = response as Request[]);
  }

  addRequest(request: Request, fileList: FileList): Promise<Number> {
    return this.serverConnection.post('requests/add', request, {withCredentials: true}).then((response) => {
      if(fileList && fileList.length > 0) {
        let file: File = fileList[0];
        let formData:FormData = new FormData();
        formData.append('file', file, file.name);
        formData.append('id', response.id);
        return this.serverConnection.post('requests/addFile', formData, {withCredentials: true}).then((fileResponse) => {
          return response as Number;
        });
      }
      else {
        return response as Number;
      }
    });
  }

  assignEmployee(request: Request): Promise<any> {
    return this.serverConnection.post(`/requests/${request.id}/assignEmployee/${request.employee.id}`, {paymentPrice: request.paymentPrice, responseDescription: request.responseDescription}).catch((error) => {
      request.employee = null;
      request.status--;
      return Promise.reject(error.message || error);
    });
  }

  setPaymentTypeCash(request: Request): Promise<any> {
    request.paymentType = PaymentType.Cash;
    return this.serverConnection.post(`/requests/payByCash`, {id: request.id});
  }

  rate(request: Request, comment: string, rate: number): Promise<any> {
    request.comment = comment;
    request.rating = rate;
    return this.serverConnection.post(`/requests/rate`, {id: request.id, rating: rate, comment: comment});
  }

  cancelRequest(request: Request): Promise<any> {
    this.requests.splice(this.requests.indexOf(request), 1);
    return this.serverConnection.post(`/requests/cancel`, {id: request.id});
  }

}



