import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Request, PaymentType } from '../../request';
import { RequestService } from '../../request.service';
import { NotificationsService } from 'angular2-notifications';

@Component({
  selector: 'app-mobile-pay-popup',
  templateUrl: './mobile-pay-popup.component.html',
  styleUrls: ['./mobile-pay-popup.component.scss']
})
export class MobilePayPopupComponent implements OnInit {
  type: PaymentType;
  request: Request;

  public PaymentType = PaymentType;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private requestService: RequestService,
    private notificationService: NotificationsService
  ) { }

  ngOnInit() {
    this.type = PaymentType.Online;
    this.route.queryParams.subscribe((params) => {
      this.request = this.requestService.getRequestById(params.requestId);
    });
  }

  changePaymentType(val: PaymentType): void {
    this.type = val;
  }

  setPayment() {
    if(this.type == PaymentType.Cash) {
      this.requestService.setPaymentTypeCash(this.request).then(() => {
        this.notificationService.success(
          " ",
          "    "
        );
      }).catch((err) => {
        this.notificationService.error(
          " ",
          err
        );
      });
      this.router.navigate([{ outlets: { popup: null }}]);
    }
    else {

    }
  }
}



