import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../authentication.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-panel-login',
  templateUrl: './panel-login.component.html',
  styleUrls: ['./panel-login.component.scss']
})
export class PanelLoginComponent implements OnInit {
  username: string;
  password: string;
  loading: boolean = false;
  errorMessage: string;
  constructor(private auth: AuthenticationService,private router: Router) { }

  ngOnInit() {
  }
  login(): void {
    this.loading = true;
    this.errorMessage = null;
    this.auth.loginPanel(this.username, this.password).then(this.successHandler).catch(this.errorHandler);
  }
  successHandler = (response) => {
    this.loading = false;
    this.router.navigate(['/panel']);
  }

  errorHandler = (error) => {
    this.loading = false;
    this.errorMessage = error;
  }
}



