import { Injectable } from '@angular/core';

@Injectable()
export class BrowserThemeService {
  redColor: string = "#ED4255";
  tealColor: string = "#50BBB6";
  orangeColor: string = "#FBB64A";

  constructor() { }
  changeColor(hashColor: string): void{
    var metaThemeColor = document.querySelector("meta[name=theme-color]");
    metaThemeColor.setAttribute("content", hashColor);
  }
}



