import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Request } from '../../request';
import { RequestService } from '../../request.service';
import { Employee } from '../../employee';
import { EmployeeService } from '../../employee.service';
import { NotificationsService } from 'angular2-notifications';

@Component({
  selector: 'app-panel-assign-employee-popup',
  templateUrl: './panel-assign-employee-popup.component.html',
  styleUrls: ['./panel-assign-employee-popup.component.scss']
})
export class PanelAssignEmployeePopupComponent implements OnInit {
  request: Request;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private employeeService: EmployeeService,
    private requestService: RequestService,
    private notificationService: NotificationsService,
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      this.request = this.requestService.getRequestById(params.requestId);
    });
  }

  submit(): void {
    this.requestService.assignEmployee(this.request).then(() => {
      this.notificationService.success(
        " ",
        "   "
      );
    }).catch((err) => {
      this.notificationService.error(
        " ",
        err
      );
    });
    this.router.navigate([{ outlets: { popup: null }}]);
  }
}



