import { Component, OnInit } from '@angular/core';
import { User } from '../../user';
import { UserService } from '../../user.service';

@Component({
  selector: 'app-panel-user-list',
  templateUrl: './panel-user-list.component.html',
  styleUrls: ['./panel-user-list.component.scss']
})
export class PanelUserListComponent implements OnInit {
  loading: boolean;
  users: User[];

  constructor(private userService: UserService) { }

  ngOnInit() {
    this.loading = true;
    this.userService.getAllUsers().then((users) => {
      this.loading = false;
      this.users = users;
    });
  }

}



