import { Injectable } from '@angular/core';
import { Employee } from './employee';
import { ServerResponse } from './server-response';
import { ServerConnectionService } from './server-connection.service';

@Injectable()
export class EmployeeService {
  employees: Employee[];

  constructor(private serverConnection: ServerConnectionService) { }

  loadEmployees(): Promise<Employee[]> {
    return this.serverConnection.get('employees').then(response => this.employees = response as Employee[]);
  }

  getEmployeeById(id: number) {
    return this.employees.find(x => x.id == id);
  }

  updateEmployee(employee: Employee): Promise<ServerResponse> {
    return this.serverConnection.post('employees/edit', employee);
  }

  updateImageUrl(employee: Employee): Promise<ServerResponse> {
    return this.serverConnection.post('employees/updateProfile', employee);
  }

  addEmployee(employee: Employee): Promise<number> {
    this.employees.push(employee);
    return this.serverConnection.post('employees/add', employee).then(response => employee.id = response as number);
  }

  removeEmployee(employee: Employee): Promise<ServerResponse> {
    this.employees.splice(this.employees.indexOf(employee), 1);
    return this.serverConnection.post('employees/remove', employee);
  }
}



